/** @format */

@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100..700;1,100..700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400..900;1,400..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lexend+Giga:wght@100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inspiration&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Aleo:ital,wght@0,100..900;1,100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Joti+One&display=swap");

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.accinfo {
  background-image: url("/Images/Rectangle 144.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.acInfo-container {
  max-width: 1440px;
  margin: auto;
}

.acinfo-wrapper {
  display: flex;
  justify-content: space-around;
  padding: 5rem 0 3rem 0;
}

.acInfo-Left {
  width: 20%;
  height: 29vh;
  background-color: #4e494956;
}

/* .vl{
    border-left: 5px solid white;
    height: 40px;
    margin: 18px 0 0 0;

    
} */

.acinfo-header h3 {
  font-family: "Inter", sans-serif;
  color: #ffffff;
  text-align: center;
  padding: 15px 0;
  cursor: pointer;
  font-size: 22px;
  border-left: 5px solid white;
  height: 20px;
  /* margin: 18px 0 0 0; */
}

.acinfo-header h3 a {
  font-weight: 800;
}

.acinfo-header h4 {
  text-align: center;
  padding: 15px 0;
}

.acinfo-header a {
  font-family: "Inter", sans-serif;
  font-weight: 300;
  color: #ffffff;
  font-size: 22px;
  cursor: pointer;
  text-decoration: none;
}

/* @@@ right column */

.acInfo-right {
  width: 50%;
}

.editInfo-header h2 {
  color: white;
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 25px;
  text-align: start;
  /* padding: 2rem 0 0 0; */
}

.editInfo-header h3 {
  color: white;
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 20px;
  text-align: start;
  padding: 2rem 0 0 0;
}

.inputsection {
  text-align: start;
  padding: 15px 0;
}

.inputsection input {
  width: 60%;
  background-color: #00000094;
  padding: 10px 50px;
  border: none;
  color: #fff;
}

.inputsection:nth-of-type(n + 1) {
  color: white;
  font-family: "Inter", sans-serif;
  font-weight: 200;
  font-size: 12px;
}
input,
label {
  display: flex;
  flex-direction: row;
  /* margin-bottom: 15px; */
}

/* @@ address */

.addSection h3 {
  color: white;
  font-family: "Inter", sans-serif;
  font-weight: 700;
}

.addSection h4 {
  color: white;
  font-family: "Inter", sans-serif;
  font-weight: 200;
  font-size: 12px;
  padding: 6px;
}

.addSection input {
  width: 60%;
  background-color: #00000094;
  padding: 10px 50px;
  border: none;
  text-align: start;
  color: #fff;
}
