body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/** @format */

* {
  margin: 0px;
  padding: 0px;
}
body {
  overflow-x: hidden;
}
.container-fluid img {
  margin-left: 40px;
  z-index: 999;
  position: relative;
}

.nav-list {
  padding: 6px;
  display: flex;
  grid-gap: 40px;
  gap: 40px;
  font-size: 24px;
  font-family: poppins;
  font-weight: 500;
  z-index: 999;
  position: relative;
}

.nav-list-1 {
  padding: 6px;
  font-size: 24px;
  cursor: pointer;
  color: #fff;
  font-family: poppins;
  font-weight: 500;
  z-index: 999;
  position: relative;
}
.nav-item a{
  position: relative;
}
.nav-item a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -6px;
  width: 100%;
  height: 3px;
  background: #e3c30d;
  border-radius: 5px;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform 0.5s;
}
.nav-item a:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}

.Icon {
  position: relative;
  top: 0px;
  left: 30px;
  color: #fff;
}
